.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/*Custom css*/

body {
    font-size: 0.875rem !important;
    overflow-y: scroll;
    overflow-x: hidden;
    letter-spacing: 0;
    color: #444;
    line-height: 1.7em !important;
    min-width: 300px;
    font-weight: 400;
    font-family: Inter,sans-serif !important;
}

.text-body-secondary{
  font-size: 0.8rem !important;
}

.card-header-title {
    font-size: 1rem !important;
    font-weight: 580;
    line-height: 1.2;
    margin-top: 0;
    margin-bottom: 0;
}

.text-right{
  text-align: right;
}
.accordion{
  border-radius: 0 !important;
}

.accordion-body{
  padding: 1rem;
}

.logo img {
    width: 85px;
    height: auto;
}

.avatar-img {
    width: 30px;
    vertical-align: baseline;
}

table, th, td {
    border-color: #e9eaed !important
}

tr th {
    font-size: .75rem !important;
    font-weight: 380;
    color: #676f7e !important;
}

td, th {
   padding: 0.5rem 1rem !important;
}

table a {
    color: rgb(25,135,84);
}

/***/
.card, .card-header {
    border-color: #e9eaed !important;
}

.card-header {
    background: #fff !important;
    /* padding: 1rem 1rem !important; */
}


/***/
.m-10{
    margin:10px;
}

.ml-7{
  margin-left: 7px;
}

.ml-5{
  margin-left: 5px;
}

.MuiTablePagination-selectLabel, .MuiTablePagination-displayedRows {
   margin-top: 0; 
   margin-bottom: 0;
}

/*navigation*/

.header{
    min-height: 60px;
    min-width: 320px;
    box-sizing: border-box;
    z-index: 9999;
    width: 100%;
    position: fixed;
    background-color: #fff;
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, .1);
}

.wrapper{
  padding:70px 20px;
}
.line{
  border-left: 2px solid #dee2e6;
}

.navbar-expand-lg{
  min-height: 60px;
}
.navbar-nav .nav-link.active, .navbar-nav .nav-link.show {
  color: rgb(25,135,84);
}

.navbar-collapse{
  flex-grow: unset;
}
.avatar{
  width: 25px; height: 25px; border-radius: 50%; text-align: center; vertical-align: middle; background-color: rgba(103, 58, 183, 0.1); font-size: 9px; font-weight: 300; color: rgb(103, 58, 183); line-height: 26px;
}

.bi-link-45deg{
  vertical-align: text-top;
  margin-left: 3px;
}
.bi-person{
  vertical-align: sub !important;
}

/*MUI */
.MuiFormControl-root{
  width: 100%;
}
.css-19syksp-MuiTableCell-root{
  line-height: 1.7em !important
}